#chat-input {
    width: 97%;
    font-size: 16px;
    padding: 10px 5px;
    border: 0px;
}

#chat-input:focus {
    outline-width: 0;
}

#chat-send {
    cursor: pointer;
    opacity: 0.5;
}

#chat-send:hover {
    opacity: 0.7;
}


